
import { login }  from '@/api/login'
import Random from '@/utils/mock'
import Mock from '@/api/mock'

import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'


import List from '@/components/common/list'
import { setInterval } from 'timers';
export default {
data() {
return {
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
randomList: [],
resString: '',
types: [
'android', 'ios', 'ios-small', 'bubbles', 'circles', 'crescent', 'dots', 'lines', 'ripple',
'spiral'
]
}
},
components: {
List, Scroller, Spinner
},
async created() {
let res = await	Mock.getData()
this.resString = JSON.stringify(res)
this.initData('1',23,'33')

login().then(data =>{

},err =>{

})
},
mounted() {
this.$nextTick(()=>{
this.$refs.scrollX.reset()
})
},
methods: {
initData() {
let sex = ['xxxx', 'aaaaaa', 'yyyyyy']
let random = [Random.dataImage('150x100', Random.email('barefeet.cn')),
Random.image('150x100', Random.color(), Random.pick(sex))]
this.randomList.push(...random)
},
pullDown() {
setTimeout(() => {
this.$refs.scrollX.donePulldown()
}, 2000);
},
pullUp() {
setTimeout(() => {
this.$refs.scrollX.donePullup()
}, 2000);
}
},
}



