import Mock from 'mockjs'

let Random = Mock.Random

export default {
	natural () {
		return Random.natural()
	},
	bool () {
		return Random.boolean(1,1, true)
	},

	email () {
		return Random.email(...arguments)
	},
	image () {
		return Random.image(...arguments)
	},
	dataImage() {
		return Random.dataImage(...arguments)
	},

	color() {
		return Random.color(...arguments)
	},
	rgb() {
		return Random.rgb(...arguments)
	},
	rgba() {
		return Random.rgba(...arguments)
	},

	// name
	name() {
		return Random.name()
	},
	cname() {
		return Random.cname()
	},

	// TEXT
	paragraph() {
		return Random.paragraph(...arguments)
	},
	cparagraph() {
		return Random.cparagraph(...arguments)
	},
	sentence() {
		return Random.sentence(...arguments)
	},
	csentence() {
		return Random.csentence(...arguments)
	},
	word() {
		return Random.word(...arguments)
	},
	cword() {
		return Random.cword(...arguments)
	},

	pick() {
		return Random.pick(...arguments)
	},

	// 时间
	// => "1983-01-29" [yyyy-MM-dd]
	date() {
		return Random.date(...arguments)
	},
	// Random.time('A HH:mm:ss')
	time() {
		return Random.time(...arguments)
	},
	// Random.datetime('yyyy-MM-dd A HH:mm:ss')
	datetime() {
		return Random.datetime(...arguments)
	},
	// Random.now('day', 'yyyy-MM-dd HH:mm:ss SS')
	now() {
		return Random.now(...arguments)
	}
}


