import BScroll from 'better-scroll'
	export default {
		data() {
			return {
				key: 'value',
				lists: [],
			}
		},
		created () {
			this.loadData()
		},
		mounted() {
			this.$nextTick(()=>{
				if (!this.scroll) {
					this.scroll = new BScroll(this.$refs.wrapper, {
						// startY: 100,
						bounce: {
							top: true,
							bottom: true,
							left: true,
							right: true
						},
						// scrollbar: true,
						// scrollbarFade: true,
						pullDownRefresh: true,
						pullDownRefreshThreshold: 30,
						pullDownRefreshStop: 10,
						// pullUpLoad: true,
						// pullUpLoadThreshold: 90,
						// pullUpLoadMoreTxt: 'xxxmore',
						// pullUpLoadNoMoreTxt: 'xxxnomore',
						// startY: 0,
						// scrollToX: 0,
						// scrollToY: -200,
						// scrollToTime: 700,
						scrollToEasing: 'bounce',
						zoom: {
							start: 1,
							min: 0.5,
							max: 1
						}
					})
					this.scroll.on('touchEnd', (pos) =>{
						console.log('end', pos)
						if (pos.y >50) {
							console.log('end > 50', pos)
							this.loadData()
						}
					})
				} else {
					this.scroll.refresh()
				}
			})
		},
		methods: {
			loadData() {
				let lists = this.lists
				lists = Object.assign(lists, [0,1,2,3])
				this.lists = lists
				setTimeout(() => {
					this.scroll.finishPullDown()
					console.log('end 2s');
					
				}, 2000);
			}
		},
	}



