import request from '@/utils/request'

export function login(params) {
  return request({
    url: 'User/getLoadingPageUrl/',
    method: 'post',
    data: params
    // data: {
    //   username,
    //   password,
    //   randomCode
    // }
  })
}

export function getRandomcode() {
  return request({
    url: '/sysuser/login/randomcode',
    method: 'get'
  })
}

export function getInfo(token) {
  return request({
    url: '/user/info',
    method: 'get',
    params: { token }
  })
}

export function logout() {
  return request({
    url: '/user/logout',
    method: 'post'
  })
}



