import Mock from 'mockjs'
import request from '@/utils/request'


const Random = Mock.Random;

// mock一组数据
let getMock = ()=> {
	return Mock.mock({
		'title': 'Syntax Demo',
		'object1|2-4': {
				'110000': '北京市',
				'120000': '天津市',
				'130000': '河北省',
				'140000': '山西省'
		},
		'array1|1': ['AMD', 'CMD', 'KMD', 'UMD'],
		'array2|1-10': ['Mock.js'],
		'array3|3': ['Mock.js'],
		'article': {
			'title':  Random.csentence(5, 30),
			'thumbnail_pic_s': Random.dataImage('300x250', 'mock的图片'),
			'author_name': Random.cname(),
			'date': Random.date() + ' ' + Random.time()
		},
	})
}
const produceNewsData = [getMock(), getMock()]

// Mock.mock(`/`, 'post', {code: 0, data: produceNewsData});
Mock.mock(`${process.env.BASE_API}/1`, 'post', produceNewsData);

export default {
	getData() {
		return request({
			url: '/1',
			method: 'post',
			data: { ...arguments }
		})
	}
}


